.picture-viewer {
  position: relative;
}

.picture-grid {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[3];
      grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

.picture-item {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.picture-item img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}

.picture-item:hover img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.picture-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.picture-modal-content {
  position: relative;
  width: 100%;
  height: auto;
  max-width: 800px;
  max-height: none;
  overflow: hidden;
  background-color: white;
  border-radius: 0.25rem;
  -webkit-box-shadow: 0 5rem 5rem rgba(0, 0, 0, 0.3);
          box-shadow: 0 5rem 5rem rgba(0, 0, 0, 0.3);
}

.picture-modal-content img {
  width: 100%;
  height: 100%;
  -o-object-fit: contain;
     object-fit: contain;
}
