.picture-viewer {
    position: relative;
}

.picture-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
}

.picture-item {
    position: relative;
    overflow: hidden;
    cursor: pointer;
}

.picture-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.2s ease;
}

.picture-item:hover img {
    transform: scale(1.1);
}

.picture-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(5px);
}

.picture-modal-content {
    position: relative;
    width: 100%;
    height: auto;
    max-width: 800px;
    max-height: none;
    overflow: hidden;
    background-color: white;
    border-radius: 0.25rem;
    box-shadow: 0 5rem 5rem rgba(0, 0, 0, 0.3);
}


.picture-modal-content img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}