.navContainer {
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    background-color: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(10px);
    z-index: 1;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;

    .navContent {
        margin-left: auto;
        margin-right: auto;
        display: grid;
        grid-auto-flow: column;
        justify-content: space-between;
        align-items: center;
        box-sizing: border-box;
        width: 75%;
    }

    // background-color: blue;
    .navPages {
        display: grid;
        grid-auto-flow: column;
        column-gap: 12px;
    }


}